@media screen and (max-width: 550px) {
  .collection-item {
    padding: 10px 10px;
    margin-top: 50px;

  }
}

.collection-item {
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    position: relative;
    padding: 0 10px;
  
    .image {
      width: 100%;
      height: 95%;
      background-size: cover;
      background-position: center;
      margin-bottom: 5px;
    }

    .custom-button {
      width: 80%;
      opacity: .7;
      position: absolute;
      top: 255px;
      display: none;
    }

    &:hover {
      .image {
        opacity: .8;
      }

      .custom-button {
        opacity: .85;
        display: flex;
      }
    }
  
    .collection-footer {
      width: 100%;
      height: 5%;
      display: flex;
      justify-content: space-between;
      font-size: 18px;
  
        .name {
            width: 90%;
            margin-bottom: 15px;
        }
    
        .price {
            width: 10%;
        }
    }
}
  