@media screen and (max-width: 550px) {
    .logo {
      height: 100%;
      width: 100%;
      margin: 0;
    }
}

.header {
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
  
    .logo-container {
      height: 100%;
      width: 100%;
      padding: 25px;
    }
  
    .options {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
  
      .option {
        padding: 10px 15px;
        cursor: pointer;
        font-weight: 900;
      }
    }
}
  